<template>
  <v-container fluid style="position: center">
    <ul class="vova" >
      <li v-for="Card in Cards" :key = "Card.id">
        <v-card
            class="mx-auto"
            max-width="344"
        >
          <v-img
              :src="Card.linkimage"
              height="200px"
          ></v-img>

          <v-card-title>
            {{ Card.name }}
          </v-card-title>

          <v-card-subtitle>
          </v-card-subtitle>

          <v-card-actions>
            <v-rating
                v-model="Card.rating"
                icon-label="custom icon label text {0} of {1}"
                color="green"
                background-color="green"
            ></v-rating>


            <v-spacer></v-spacer>
            {{ Card.like }}
            <v-btn
                icon
                color="green"
                @click="Card.like++"
            >
              <v-icon>mdi-heart</v-icon>
            </v-btn>
            <v-btn
                icon
                @click="Card.show = !Card.show"
            >
              <v-icon>{{ Card.show ? 'mdi-chevron-up' : 'mdi-chevron-down' }}</v-icon>
            </v-btn>
          </v-card-actions>

          <v-expand-transition>
            <div v-show="Card.show">
              <v-divider></v-divider>

              <v-card-text>
                {{Card.textcard}}
              </v-card-text>
            </div>
          </v-expand-transition>
        </v-card>
      </li>
    </ul>
  </v-container>



</template>

<script>
export default {
  data: () => ({
    Cards:[
      {id:1,textcard:"Смешной котик",show:false,linkimage:"https://i08.fotocdn.net/s120/b4a0febe1043eccb/public_pin_l/2742790978.jpg",name:"МистерКэт",rating: 0,like:0},
      {id:2,textcard:"Очень красивый котик",show:false,linkimage:"https://pic.rutubelist.ru/video/17/b1/17b100a0bcbc6e5e8d11101cde21aca7.jpg",name:"Пухляш",rating: 0,like:0},
      {id:3,textcard:"Когда вырастет, станет Сугробиком",show:false,linkimage:"https://mediasole.ru/data/images/133/133936/662478--matilda-p.jpg",name:"Снежок",rating: 0,like:0},
    ]

    //show: false,
  })
}
</script>

<style scoped>
.vova{
  display: grid;
  grid-template-columns: 2fr 2fr 2fr;
  list-style-type:none;
  row-gap:10px;
  column-gap: 100px;

}
</style>
